*{
  box-sizing: border-box;
  margin: 0;
}

body{
  width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  padding: 30px;
  overflow-x: hidden;
}
main {
  margin-top: 5px;
  width: 90vw;
  max-width: 550px;
  min-height: 750px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: #F5F7FB;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Karla', sans-serif;
  background: url(/quiz-react/static/media/47fbb68a37492662cd74715d7787a212.d550f629.jpg), rgba(0, 0, 0, 0.685);
  background-blend-mode:overlay;
  background-size: cover;
  border-radius: 30px;
  background-position: top;
}

.question {
    border: none;
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.question__text{
    font-size: 18px;
    padding-top: 30px;
    text-align: center;
}

.question:first-of-type .question__text{
    padding-top: 0;
}

.question input[type=radio] {
	display: none;
}

.question label {
	cursor: pointer;
	padding: 0px 15px;
	line-height: 14px;
	border: 1px solid #999;
	border-radius: 30px;
	-webkit-user-select: none;
	        user-select: none;
    background-color: aliceblue;
    color: black;
    width: 200px;
    height: 55px;
    margin-top: 10px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
 
/* Checked */
.question input[type=radio]:checked + label {
	background: #ffe0a6;
}

.question input[type=radio]:checked + label.results{
	background: #fd7575;
    border: 1px solid black
}
 
/* Hover */
.question label:hover {
	color: #666;
}

.red {
    background-color: aquamarine !important;
    border: 1px solid black
}
.quiz__page {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    padding: 30px;
    color: white;
    min-height: 750px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.quiz__submit {
    cursor: pointer;
    padding: 0px 15px;
    line-height: 14px;
    border-radius: 30px;
    background-color: aliceblue;
    font-size: 20px;
    color: black;
    width: 70%;
    height: 55px;
    margin-top: 45px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.quiz__submit:disabled {
    background-color: rgba(128, 128, 128, 0.644);
    cursor: not-allowed;
}

.quiz__result {
    font-size: 26px;
    margin: 20px;
    text-align: center;
}

.quiz__reload {
    cursor: pointer;
    padding: 0px 15px;
    line-height: 14px;
    border-radius: 30px;
    background-color: aliceblue;
    font-size: 20px;
    color: black;
    width: 70%;
    height: 55px;
    margin-top: 10px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.quiz__reload:hover {
    background-color: rgb(2, 255, 171);
}
.intro {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: white;
    padding: 20px;
    height: 750px;
}

.intro__heading {
    margin-top: 150px;
    font-size: 48px;
    font-weight: 800;
    text-shadow: 0px 0px 25px rgba(255, 255, 255, 1);
}

.intro__description {
    margin-top: 30px;
    font-size: 30px;
    text-shadow: 0px 0px 25px rgba(255, 255, 255, 1);
}

.start__quiz {
    margin-top: 60px;
    width: 250px;
    height: 60px;
    border-radius: 30px;
    background-color: white;
    font-size: 18px;
    cursor: pointer;
}

.start__quiz:hover {
    background-color: rgb(93, 255, 201);
}

.select_category__label {
    margin-top: 50px;
    font-size: 20px;
    text-transform: uppercase;
}

#select_category {
    margin-top: 10px;
    width: 250px;
    height: 30px;
    font-size: 18px;
    border-radius: 5px;
    text-align: center;
}

.quiz__clear_stats {
    margin-top: auto;
    width: 180px;
    height: 30px;
    border: none;
    text-decoration: underline;
    background: none;
    font-size: 18px;
    cursor: pointer;
    color: white;
}
